.movieList {
   position: relative;
   min-height: 240px;
}

.movieItem {
   display: grid;

   .wrapper {
      width: 100%;
      padding: 1rem;
   }

   .movieItemDetails {
      display: grid;
      row-gap: 0;
      column-gap: 1rem;
      grid-template: auto 1fr/70px minmax(0, 1fr);

      @media (min-width: 768px) {
         grid-template: auto 1fr/120px minmax(0, 1fr);
      }
   }

   .image {
      grid-column-start: 1;
      grid-row: span 2 / span 2;
      grid-row-start: 1;

      border-radius: 0.25rem;
      overflow: hidden;
      background-color: rgb(229, 229, 229);

      height: 105px;

      @media (min-width: 768px) {
         height: 180px;
      }

      img {
         width: 100%;
         height: 100%;
      }
   }

   .text {
      grid-column-start: 2;

      .ageLimit {
         display: flex;
         margin-bottom: 0.25rem;

         span {
            display: inline-flex;
            align-items: center;
            height: 1.25rem;
            border-radius: 0.125rem;
            padding: 0 0.5rem;
            line-height: 1rem;
            font-size: 0.75rem;
            font-weight: 600;
            color: rgba(255, 255, 255, 0.95);
            background-color: #ddbc3f;
            transform: scale(0.9);
         }
      }

      .title {
         font-size: 1rem;
         font-weight: 600;
         color: rgb(38, 38, 38);
         line-height: 1.25;
      }

      .subtitle {
         font-size: 0.8125rem;
         line-height: 1.25;
         margin-top: 0.25rem;
         color: rgb(163, 163, 163);
         text-transform: capitalize;
      }
   }

   .showTime {
      grid-column: span 2 / span 2;
      grid-column-start: 1;

      @media (min-width: 768px) {
         grid-column-start: 2;
      }

      .showTimeDetails {
         padding: 1rem 0;

         span {
            display: block;
            margin-bottom: 0.5rem;
            font-size: 0.875rem;
            line-height: 1.25rem;
            font-weight: 600;
         }
      }

      .showTimeList {
         display: grid;
         gap: 0.75rem;
         grid-template-columns: repeat(2, minmax(0, 1fr));

         @media (min-width: 360px) {
            grid-template-columns: repeat(3, minmax(0, 1fr));
         }

         @media (min-width: 768px) {
            grid-template-columns: repeat(2, minmax(0, 1fr));
         }

         @media (min-width: 860px) {
            grid-template-columns: repeat(3, minmax(0, 1fr));
         }
      }

      .showTimeItem {
         border: 1px solid rgb(56, 189, 248);
         background-color: rgba(224, 242, 254, 0.5);
         border-radius: 0.375rem;
         padding: 0.25rem 0.5rem;
         font-size: 0.8125rem;
         text-align: center;
         color: rgb(2, 132, 199);
         white-space: nowrap;

         cursor: pointer;

         strong {
            font-size: 0.9375rem;
            font-weight: 600;
         }
      }
   }
}
